@import url("https://fonts.googleapis.com/css2?family=Golos+Text:wght@400..900&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Golos Text", sans-serif;
  color: #1c3749;
  background-color: #ebf1f8;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

.container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 10px;
  width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
}

.title {
  font-weight: 500;
  font-size: 44px;
  line-height: 102%;
  letter-spacing: -0.02em;
  color: #161618;
}
@media screen and (max-width: 460px) {
  .title {
    font-size: 30px;
  }
}

.header-container {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.header-container ul {
  display: flex;
  gap: 40px;
}

.header-items li {
  font-size: 16px;
  cursor: pointer;
  font-weight: 500;
  color: #161618;
}

.header-button {
  display: block;
  width: 193px;
  cursor: pointer;
  height: 38px;
  background-color: #161618;
  color: #fff;
  font-size: 14px;
  text-align: center;
  border-radius: 5px;
  text-decoration: none;
  border: 0;
}

.header-button:hover {
  background-color: #333;
}

.header-burger {
  display: none;
  height: 34px;
  width: 34px;
}

.logo {
  width: 147px;
  height: 49px;
}

.start {
  margin-bottom: 20px;
}

.start-container {
  background-color: #161618;
  background-image: url("./img/start-image.png");
  background-size: 50%;
  background-repeat: no-repeat;
  background-position: right;
  color: white;
  padding: 60px;
  border-radius: 30px;
  display: flex;
  flex-direction: column;
}

.start-badges {
  display: flex;
  gap: 10px;
  margin-bottom: 50px;
}

.start-badges span {
  font-size: 16px;
  padding: 16px;
  border: 1px solid white;
  border-radius: 30px;
}

.start-text {
  font-size: 22px;
  font-weight: 500;
  margin-bottom: 20px;
}

.start-heading {
  font-size: 58px;
  font-weight: 600;
  max-width: 630px;
  line-height: 92%;
  margin-bottom: 35px;
}

.start-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 40px;
  max-width: 600px;
}

.start-list li {
  color: #a6a6a6;
  font-weight: 400;
  padding-left: 27px;
  position: relative;
}

.start-list li::before {
  content: "";
  position: absolute;
  left: 0;
  width: 17px;
  height: 17px;
  background-image: url("./img/check.svg");
}

.start-buttons {
  display: flex;
  gap: 20px;
}

.start-buttons button {
  max-width: 213px;
  cursor: pointer;
  border: 1px solid white;
  padding: 20px 30px;
  font-size: 16px;
  color: white;
  background: 0;
  border-radius: 10px;
  font-weight: 600;
}

.start-buttons-blue {
  background-color: #2a5cd9 !important;
  border: 0 !important;
}

.icon-list-list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 12px;
}
@media screen and (max-width: 460px) {
  .icon-list-list {
    grid-template-columns: 1fr;
  }
}

.icon-list-item {
  background-color: #fff;
  border-radius: 28px;
  width: 100%;
  padding: 20px;
}

.icon-list-item div {
  display: flex;
  width: 100%;
  margin-bottom: 12px;
  align-items: center;
  gap: 12px;
}

.icon-list-item div h2 {
  font-size: 16px;
  font-weight: 600;
}

.icon-list-item p {
  font-size: 12px;
  font-weight: 400;
  letter-spacing: -2%;
  line-height: 126%;
  max-width: 290px;
  padding-bottom: 25px;
}

.start-image {
  display: none;
}

.about-coach {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-top: 100px;
}
@media screen and (max-width: 460px) {
  .about-coach {
    margin-top: 40px;
  }
}

.about-coach-top,
.about-coach-bottom {
  display: flex;
  gap: 12px;
  align-items: stretch;
}

@media screen and (max-width: 1000px) {
  .about-coach-top {
    flex-direction: column;
  }
  .about-coach-bottom {
    flex-direction: column-reverse;
  }
}
.about-coach-top {
  margin-bottom: 20px;
}
.about-coach-top-image-wrapper {
  min-width: 578px;
  border-radius: 45px;
  overflow: hidden;
}
@media screen and (max-width: 460px) {
  .about-coach-top-image-wrapper {
    min-width: 100%;
    width: 100%;
    height: 266px;
    border-radius: 18px;
  }
  .about-coach-top-image-wrapper img {
    width: 100%;
    object-fit: contain;
    object-position: center;
  }
}
.about-coach-top-text {
  background: white;
  border-radius: 45px;
  padding: 40px;
}
@media screen and (max-width: 1000px) {
