    padding: 20px;
    border-radius: 18px;
  }
}
.about-coach-top-text h1 {
  color: #161618;
  font-size: 44px;
  font-weight: 500;
  margin-bottom: 40px;
}
@media screen and (max-width: 1000px) {
  .about-coach-top-text h1 {
    font-size: 30px;
    margin-bottom: 20px;
  }
}

.about-coach-top-text p,
.about-coach-bottom-text p {
  font-size: 18px;
  margin-bottom: 20px;
  font-weight: 400;
  line-height: 130%;
}
.about-coach-top-text p:last-child,
.about-coach-bottom-text p:last-child {
  margin: 0;
}
@media screen and (max-width: 460px) {
  .about-coach-top-text p,
  .about-coach-bottom-text p {
    font-size: 14px;
    margin-bottom: 10px;
  }
}

.about-coach-bottom-text {
  padding: 40px;
  background-color: #4c77aa;
  border-radius: 45px;
}
@media screen and (max-width: 460px) {
  .about-coach-bottom-text {
    border-radius: 18px;
    padding: 20px;
  }
}
.about-coach-bottom-text h1 {
  font-size: 44px;
  color: white;
  line-height: 102%;
  font-weight: 500;
  margin-bottom: 20px;
}
@media screen and (max-width: 460px) {
  .about-coach-bottom-text h1 {
    font-size: 30px;
    margin-bottom: 20px;
  }
}
.about-coach-bottom-text p {
  color: white;
  font-weight: 400;
}

.about-coach-bottom-image-wrapper {
  border-radius: 45px;
  min-width: 578px;
  height: 346px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: white;
}
@media screen and (max-width: 460px) {
  .about-coach-bottom-image-wrapper {
    max-width: 100%;
    height: auto;
    min-width: 100%;
    border-radius: 18px;
  }
  .about-coach-bottom-image-wrapper img {
    width: 70%;
    object-fit: contain;
  }
}

.base-list {
  margin-top: 100px;
}
.base-list-wrapper {
