.base-list-listing {
  display: flex;
  flex-direction: column;
  gap: 10px;
  font-size: 14px;
  line-height: 130%;
  color: #161618;
}

.how-much {
  display: flex;
  gap: 41px;
  background: #161618;
  color: white;
  border-radius: 45px;
