  padding: 20px;
}

@media screen and (max-width: 1000px) {
  .container {
    padding: 10px;
  }
  .header-burger {
    display: block;
  }
  .header-container ul,
  .header-items,
  .header-items li {
    display: none;
  }
  .header-button {
    display: none;
  }
  .start-badges {
    justify-content: center;
  }
  .start-badges span {
    font-size: 12px;
    padding: 12px 16px;
  }
  .start-container {
    padding: 14px 20px;
  }
  .start-text {
    font-size: 18px;
    text-align: center;
  }
  .start-container,
  .start {
    background-image: none;
  }
  .start-heading {
    font-size: 32px;
    text-align: center;
  }
  .start-buttons {
    margin-bottom: 10px;
  }
  .start-list li {
    font-size: 14px;
  }
  .start-buttons {
    flex-direction: column;
    width: 100%;
  }
  .start-heading {
    max-width: 100%;
  }
  .start-buttons button {
    max-width: 100%;
  }
  .icon-list {
    flex-direction: column;
  }
  .icon-list-item {
    padding-bottom: 0;
  }
  .start-image {
    display: block;
  }
}
